  top: 336px;
  left: 379px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.vector-icon {
  position: relative;
  width: 16px;
  height: 16px;
}
.vector-wrapper {
  border-radius: var(--br-9xs);
  background-color: var(--subtle-blue);
  width: 32px;
  height: 32px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  padding: 0 var(--padding-7xs);
  box-sizing: border-box;
}
.soft-skills-hr {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 150%;
  display: inline-block;
  width: 202px;
}
.soft-skills-hr-interview-ap-wrapper {
  position: relative;
  width: 202px;
  height: 54px;
}
.frame-parent101 {
  position: absolute;
  top: 428px;
  left: 379.04px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-base);
}
.fade-in-left1,
.fade-in-right1 {
  position: absolute;
  top: 45px;
  left: 743px;
  background: linear-gradient(270deg, rgba(255, 255, 255, 0), #fff);
  width: 201px;
  height: 486px;
  overflow: hidden;
}
.fade-in-right1 {
  top: 71px;
  left: 1439px;
  transform: rotate(180deg);
  transform-origin: 0 0;
  display: none;
}
.placement-support,
.student-wall1 {
  position: relative;
  width: 1440px;
  overflow: hidden;
  flex-shrink: 0;
  display: none;
}
.placement-support {
  height: 587px;
  text-align: left;
  font-size: var(--font-size-lg);
  color: var(--text-label-colors-charcoal);
  font-family: var(--font-font);
}


.student-testimonials {
  position: relative;
  letter-spacing: 0.08em;
  text-transform: uppercase;
  background: linear-gradient(-83.03deg, #992fed, #2f80ed);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}




















